@media screen and (max-width: 1170px) {
  .wrapper {
    margin: auto;
    width: 100%;
  }
  section {
    max-width: 100vw;
  }
  .main-banner {
    width: 100%;
  }
  .img-wrapper {
    width: 100%;
    height: 650px;
  }
  .main-banner img {
    width: 100%;
  }
  .user-grid {
    margin: 0 60px;
  }
  .user-grid {
    display: grid;
    grid-template-columns: repeat(3, 282px);
    grid-template-rows: 1fr 1fr;
    gap: 29px;
    justify-content: center;
  }
  .user .user-info * {
    max-width: 242px;
    margin: 5px 0;
  }
  .user .user-info .user-name {
    margin: 20px 0;
  }
  .header-wrapper {
    width: auto;
  }
  .btn-wrapper {
    margin-right: 32px;
  }
  .logo {
    margin-left: 32px;
  }
}

@media screen and (max-width: 1024px) {
  .user-grid {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 1fr 1fr;
    gap: 29px;
  }
}
@media screen and (max-width: 768px) {
  .user-grid {
    margin: 0 32px;
    grid-template-columns: 1fr 1fr;
    gap: 16px;
  }
  .user .user-info * {
    max-width: 320px;
  }
}

@media screen and (max-width: 550px) {
  .banner-card-wrapper {
    padding: 0 16px;
  }
  .user {
    max-width: 100%;
  }
  .user-grid {
    grid-template-columns: 1fr;
  }
  .btn-wrapper {
    margin-right: 16px;
  }
  .logo {
    margin-left: 16px;
  }
  form > * {
    width: 80%;
  }
  form > input {
    width: 80%;
  }
  form input {
    width: 267px;
    max-width: 300px;
  }
  section:last-of-type {
    margin-bottom: 10px;
  }
  h1 {
    padding: 0 16px;
  }
}

@media screen and (max-width: 400px) {
}

@media screen and (max-width: 360px) {
  .user-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }
}
